<template>
  <div id="backgroundImage" class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center top-">
        <b-col md="5">
          <img class="navbar-brand-full justify-content-center" src="tout-paie-web/img/brand/logoImg.png" width="350" height="80" style="display: block;margin-left: auto;margin-right: auto;margin-bottom: 13%;">
          <b-card-group>
            <b-card no-body class="p-3">
              <b-card-body class="card-padding">
                <b-form @submit.stop.prevent="showLoader=true,login()">
                  <p class="text-muted">Sign In to your account</p>
                  <b-input-group class="mb-3" validated>
                    <b-input-group-prepend><b-input-group-text><i class="icon-user"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="text" v-model="email" class="form-control" placeholder="Username"/>
                  </b-input-group>
                  <b-row>
                    <b-col cols="12" class="text-right">
                      <b-button variant="link" style="color: #ef402b" to="/forgot-password" class="px-0">Forgot password?</b-button>
                    </b-col>
                  </b-row>
                  <b-input-group class="mb-4" validated>
                    <b-input-group-prepend><b-input-group-text><i class="icon-lock"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="password" v-model="password" class="form-control" placeholder="Password"/>
                  </b-input-group>
                  <b-row>
                    <b-col cols="3">

                    </b-col>
                    <b-col cols="6">
                      <b-button type="submit" :disabled="!email || !password" v-if="showLoader==false" class="px-6 log-in">Login</b-button>
                       <!-- <b-button variant="primary" v-if="showLoader==true">
                        <b-spinner small></b-spinner>
                        <span class="sr-only">Loading...</span>
                      </b-button>  -->
                      <b-button variant="primary" v-if="showLoader==true">
                        <b-spinner small type="grow"></b-spinner>
                        Loading...
                      </b-button>
                    </b-col>
                  </b-row>
                </b-form>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import { Switch as cSwitch } from '@coreui/vue'
import * as toutPaieService from "../toutPaie/ToutPaieService.js";
import VueNotifications from 'vue-notifications'

export default {
   name: 'Login',
  components: {
    cSwitch
  },
  data: () => {
    return {
      user: {},
      email: '',
      password: '',
      showLoader:false
    }
  },
  methods: {
    login: function() {
        this.user.Email = this.email;
        this.user.password = this.password;
        this.user.From="W";
        // let data = {
        //   password : 'kailash1234',
        //   Email: "kailash.mindnerves@gmail.com"
        // }
        toutPaieService.login(this.user).then(res => {
          let message = res.message.split('_').join(' ');
          /* var res = {
            "code": 200,
            "message": "Login Successfully",
            "user": {
              "id": 1,
              "active": true,
              "firstName": "Admin",
              "lastName": "Test",
              "userRole": "SuperAdmin",
              "passwordSet": true,
              "mobileNumber": null,
              "countryCode": null,
            },
            "token": "eyJhbGciOiJIUzUxMiJ9..7cPg0lxNjv2f_D254iU-eky3tV0pSFlr5XZTnfB62lDhBAnNakSvAuW7VtPf8mgu8GiB3RB0O_eTOtNCvZbKRA",
            "status": "success"
          };*/
          if(res.status == 'Success') {
            let user = res.data;
           // user.Role = 'SM';
           var d = new Date();
            window.localStorage.setItem("auth_token", res.data.headers.RememberToken);
            window.localStorage.setItem('loginUser', JSON.stringify(user));
            window.localStorage.setItem('loginUserDate',d.getDate() );
            console.log("loginUserDate====>",window.localStorage.getItem('loginUserDate'));
            if(user.Role == 'SA')
            {
              this.$router.push('managers');
            }else if(user.Role == 'SM')
            {
              this.$router.push('Products');   
            }else
            if(user.Role == 'M')
            {
              this.$router.push('Stores');
               
            } else
            {
              this.$router.push('dashboard');
            }
            this.showLoader=false;
            
            VueNotifications.success({title: this.translateString('Success'), message: message})
          } else if (res.code == 203) {
            this.showLoader=false;
            VueNotifications.warn({title: this.translateString('Warning') , message: message});
            this.$router.push('/reset-password');
          } else {
             this.showLoader=false;
            VueNotifications.error({title: this.translateString('Error') , message: message})
          }
        });
        // });
    },
    translateString : function(colName) {
      return this.$i18n.t(colName);
    },
   },

    beforeMount() {
     // VueNotifications.success({message: 'Welcome to ToutPaie.',timeout:2000});
      //      toutPaieService.logout().then((res) => {
      // });
       window.localStorage.removeItem("auth_token");
      window.localStorage.removeItem("loginUser");

    }
}
</script>
